import React, { useState, useEffect, useRef } from 'react';

// This component renders a stack of cards that spread out on scroll.
const AnimatedCardStack = () => {
  const containerRef = useRef(null);
  const [scrollProgress, setScrollProgress] = useState(0);

  // Card data for rendering, including title and a placeholder image.
  const cards = [
    { id: 1, title: 'Card One', imageUrl: 'https://placehold.co/400x300/F0F4F8/6B7280?text=Card+1' },
    { id: 2, title: 'Card Two', imageUrl: 'https://placehold.co/400x300/E1E5E9/6B7280?text=Card+2' },
    { id: 3, title: 'Card Three', imageUrl: 'https://placehold.co/400x300/D2D6DC/6B7280?text=Card+3' },
    { id: 4, title: 'Card Four', imageUrl: 'https://placehold.co/400x300/C3C7CE/6B7280?text=Card+4' },
    { id: 5, title: 'Card Five', imageUrl: 'https://placehold.co/400x300/B4B8C1/6B7280?text=Card+5' },
  ];

  // Effect to handle scroll-triggered animation.
  useEffect(() => {
    const handleScroll = () => {
      if (!containerRef.current) return;

      const { top, height } = containerRef.current.getBoundingClientRect();
      const windowHeight = window.innerHeight;
      
      // Calculate scroll progress from 0 (at the top of the viewport) to 1 (at the bottom of the viewport).
      let progress = (windowHeight - top) / (windowHeight + height);
      progress = Math.min(1, Math.max(0, progress));
      setScrollProgress(progress);
    };

    window.addEventListener('scroll', handleScroll);
    handleScroll(); // Call on mount to set initial position

    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <>
      <script src="https://cdn.tailwindcss.com"></script>
      <div className="min-h-[200vh] flex justify-center items-start pt-[25vh] bg-gray-100 font-sans overflow-hidden">
        <div ref={containerRef} className="relative w-full max-w-sm sm:max-w-md lg:max-w-xl">
          {cards.map((card, index) => {
            // Calculate dynamic values for transform and rotation based on scroll progress.
            const spreadDistance = 250; // Distance each card spreads vertically
            const rotationAngle = 10;   // Maximum rotation for the end cards
            const zIndex = cards.length - index;

            const translateY = (index * spreadDistance) * scrollProgress;
            const rotate = (index - (cards.length - 1) / 2) * rotationAngle * scrollProgress;

            return (
              <div
                key={card.id}
                className={`
                  absolute
                  w-full
                  bg-white
                  shadow-xl
                  rounded-2xl
                  p-6
                  transition-all
                  duration-300
                  ease-out
                `}
                style={{
                  top: `0px`,
                  zIndex: zIndex,
                  transform: `translateY(${translateY}px) rotate(${rotate}deg) scale(1)`,
                  transformOrigin: 'top center',
                }}
              >
                <img src={card.imageUrl} alt={card.title} className="w-full rounded-xl mb-4" />
                <h2 className="text-xl sm:text-2xl font-semibold text-gray-800">{card.title}</h2>
                <p className="text-gray-500 mt-2">This is placeholder content for the card.</p>
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
};

export { AnimatedCardStack };
